import XLSX from "xlsx";


export const downloadExl = function (json, downName) {
    let wb = XLSX.utils.book_new();
    let ws = XLSX.utils.json_to_sheet(json, { skipHeader: true });
    XLSX.utils.book_append_sheet(wb, ws, 'Sheet');
    return XLSX.writeFile(wb, downName);
};


export const downloadExl2 = function (json, downName, type) {  // 导出到excel
    console.log('json', json);
    let keyMap = []; // 获取键
    for (let k in json[0]) {
        keyMap.push(k);
    }
    let tmpdata = []; // 用来保存转换好的json
    json.map((v, i) => keyMap.map((k, j) => Object.assign({}, {
        v: v[k],
        position: (j > 25 ? this.getCharCol(j) : String.fromCharCode(65 + j)) + (i + 1)
    }))).reduce((prev, next) => prev.concat(next)).forEach((v) => {
        tmpdata[v.position] = {
            v: v.v
        };
    });
    let outputPos = Object.keys(tmpdata);  // 设置区域,比如表格从A1到D10
    let tmpWB = {
        SheetNames: ['mySheet'], // 保存的表标题
        Sheets: {
            'mySheet': Object.assign({},
                tmpdata, // 内容
                {
                    '!ref': outputPos[0] + ':' + outputPos[outputPos.length - 1] // 设置填充区域
                })
        }
    };
    let tmpDown = new Blob([s2ab(XLSX.write(tmpWB,
        { bookType: type === undefined ? 'biff2' : type, bookSST: false, type: 'binary' } // 这里的数据是用来定义导出的格式类型
    ))], {
            type: ''
        });  // 创建二进制对象写入转换好的字节流
    let href = URL.createObjectURL(tmpDown);  // 创建对象超链接
    let aDom = document.createElement("a");
    aDom.download = downName + '.xls';  // 下载名称
    aDom.href = href;  // 绑定a标签
    aDom.click();  // 模拟点击实现下载
    setTimeout(() => {  // 延时释放
        URL.revokeObjectURL(tmpDown); // 用URL.revokeObjectURL()来释放这个object URL
    }, 100);
};

function s2ab(s) { // 字符串转字符流
    let buf = new ArrayBuffer(s.length);
    let view = new Uint8Array(buf);
    for (let i = 0; i !== s.length; ++i) {
        view[i] = s.charCodeAt(i) & 0xFF;
    }
    return buf;
}
