<template>
    <div id="tableDom">
        <div class="bts">
            <!-- <el-tooltip content="垂直翻转" placement="top">
                <el-button style="float:left" plain icon="el-icon-sort" circle @click="rotate('cz')"/>
            </el-tooltip>
            <el-tooltip content="水平翻转" placement="top">
                <el-button style="float:left" class="sp" plain icon="el-icon-sort" circle @click="rotate('sp')"/>
            </el-tooltip>-->
            <el-tooltip content="清空" placement="top">
                <el-button type="danger" plain v-loading.fullscreen.lock="loading.get" icon="el-icon-close" circle @click="empty()"/>
            </el-tooltip>
            <el-tooltip content="刷新" placement="top">
                <el-button type="warning" plain v-loading.fullscreen.lock="loading.get" icon="el-icon-refresh" circle @click="refresh()"/>
            </el-tooltip>
            <el-tooltip content="打印" placement="top">
                <el-button type="info" plain :loading="loading.save" icon="el-icon-printer" circle @click="printf()"/>
            </el-tooltip>
            <el-tooltip content="保存" placement="top">
                <el-button type="success" plain :loading="loading.save" icon="el-icon-receiving" circle @click="save()"/>
            </el-tooltip>
            <el-tooltip content="导出" placement="top">
                <el-button type="primary" plain icon="el-icon-download" circle @click="exportExl()"/>
            </el-tooltip>
        </div>
        <div class="msg">{{msg}}-</div>
        <div class="wrap" ref="wrap">
            <PeopleWrap position="top" :arr="arrTop"></PeopleWrap>
            <div class="tableWrap">
                <div class="tabel"></div>
                <PeopleWrap position="right" :arr="arrRight"></PeopleWrap>
            </div>
            <PeopleWrap position="bottom" :arr="arrBottom"></PeopleWrap>
        </div>
        <div class="other">
            <el-form :inline="true" size="mini">
                <el-form-item label="空白桌牌填充">
                    <el-input v-model.trim="emptyInput"/>
                </el-form-item>
                <el-form-item>
                    <el-button type="danger" @click="emptyName('fill')">填充</el-button>
                    <el-button type="info" @click="emptyName('del')">删除</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>
<script>
import { Loading } from "element-ui";
import PeopleWrap from "@components/peopleWrap/peopleWrap.vue";
import { getLast, saveLast } from "@service/tableCard.js";
import { downloadExl } from "@service/xlsx.js";

export default {
    name: "tableDom",
    mounted() {
        this.getLast();
    },
    data: () => ({
        emptyInput: "",
        loading: {
            get: false,
            save: false
        },
        arr: [],
        msg: ""
    }),
    computed: {
        arrHandleName() {
            return this.arr.map(v => {
                if (v) {
                    if (v.name.length === 2) {
                        v.name = v.name.replace(/(.{1})/, `$1　`);
                    }
                }
                return v;
            });
        },
        ajaxJson() {
            return this.arrHandleName.map(v => {
                return { name: v.name, index: v.index };
            });
        },
        arrTop() {
            return this.arrHandleName.filter(v => v.position == "top");
        },
        arrRight() {
            return this.arrHandleName.filter(v => v.position == "right");
        },
        arrBottom() {
            return this.arrHandleName.filter(v => v.position == "bottom");
        }
    },
    watch: {
        arrHandleName: {
            handler: function(n, o) {
                this.msg = "有数据未保存";
            },
            deep: true
        }
    },
    methods: {
        emptyName(type) {
            switch (type) {
                case "fill":
                    this.arr.forEach(v => {
                        if (v.name == "") v.name = this.emptyInput;
                    });
                    break;
                case "del":
                    this.arr.forEach(v => {
                        if (v.name == this.emptyInput) v.name = "";
                    });
                    break;
            }
        },
        exportExl() {
            this.save();
            let json = this.arrHandleName.map((v, i) => {
                let bSize = 250;
                if (v.name.length >= 5 && v.name.length <= 7) {
                    bSize = 130;
                } else if (v.name.length >= 8) {
                    bSize = 100;
                }
                return {
                    桌牌ID: v.index,
                    顶端文字: "",
                    中间文字: v.name,
                    底端文字: "",
                    顶端大小: 100,
                    中间大小: bSize,
                    底端大小: 100
                };
            });
            json.unshift({
                桌牌ID: "桌牌ID",
                顶端文字: "顶端文字",
                中间文字: "中间文字",
                底端文字: "底端文字",
                顶端大小: "顶端大小",
                中间大小: "中间大小",
                底端大小: "底端大小"
            });
            downloadExl(json, "桌牌.xls");
        },
        getLast() {
            if (this.loading.get) return;
            this.loading.get = true;
            getLast()
                .then(res => {
                    if (res.data.statusCode != 200)
                        throw new Error(res.data.msg);
                    let arr = res.data.result;
                    if (arr.length != 34) {
                        for (let i = 0; i < 34; i++) {
                            arr.push({
                                index: i + 1,
                                name: ""
                            });
                        }
                    }
                    this.arr = arr.map((v, i) => {
                        let position = "right";
                        if (v.index <= 16) position = "top";
                        if (v.index >= 19) position = "bottom";
                        return {
                            index: v.index,
                            name: v.name,
                            position,
                            isEdit: false
                        };
                    });
                    this.$nextTick(() => {
                        this.msg = "";
                    });
                    this.$notify({
                        title: "成功",
                        message: res.data.msg,
                        type: "success"
                    });
                })
                .catch(err => {
                    console.log("err", err);
                    this.$notify.error({
                        title: "错误",
                        message: err.message
                    });
                })
                .finally(() => {
                    this.loading.get = false;
                });
        },
        save() {
            if (this.loading.save) return;
            this.loading.save = true;
            saveLast(this.ajaxJson)
                .then(res => {
                    if (res.data.statusCode != 200)
                        throw new Error(res.data.msg);
                    this.$notify({
                        title: "成功",
                        message: res.data.msg,
                        type: "success"
                    });
                    this.$nextTick(() => {
                        this.msg = "";
                    });
                })
                .catch(err => {
                    this.$notify.error({
                        title: "错误",
                        message: err.message
                    });
                })
                .finally(() => {
                    this.loading.save = false;
                });
        },
        printf() {
            this.save();
            print.portrait = false;
            window.print();
        },
        loadingHandle(state) {
            if (state) {
                Loading.service({ fullscreen: true });
            } else {
                Loading.close();
            }
        },
        rotate(type) {
            let elm = this.$refs.wrap;
            switch (type) {
                case "sp":
                    elm.classList.toggle("sp");
                    break;
                case "cz":
                    elm.classList.toggle("cz");
                    break;
            }
        },
        refresh() {
            history.go(0);
        },
        empty() {
            this.arr = this.arr.map(v => {
                v.name = "";
                return v;
            });
        }
    },
    components: {
        PeopleWrap
    }
};
</script>
 <style  lang="scss">
.wrap {
    transition: all 0.3s;
}
.wrap.sp {
    transform: scaleX(-1);
    .people {
        transform: scaleX(-1) !important;
    }
}

.wrap.cz {
    transform: scaleY(-1);
    .people {
        transform: scaleY(-1) !important;
    }
}
@media print {
    .head,
    .bts,
    .other,
    .el-notification {
        display: none;
    }
}
</style>
<style lang="scss" scoped>
.msg {
    text-align: right;
    font-size: 14px;
    color: rgba(245, 108, 108, 0.8);
}
.bts {
    margin-bottom: 30px;
    .sp {
        transform: rotate(90deg);
    }
}
#tableDom {
    text-align: right;
    display: inline-block;
    position: relative;
    .tableWrap {
        position: relative;
        .tabel {
            margin-left: 50px;
            width: 1210px;
            height: 300px;
            display: inline-block;
            background: rgba(211, 220, 230, 0.8);
            border-radius: 5px;
            position: relative;
            &::before {
                content: "TV";
                display: inline-block;
                position: absolute;
                top: 0;
                transform: rotate(180deg);
                left: -50px;
                background: rgba(211, 220, 230, 0.8);
                padding: 2px;
                box-sizing: border-box;
                writing-mode: tb;
                height: 100%;
                width: 20px;
                text-align: center;
                border-radius: 3px;
            }
            &::after {
                content: "";
                height: 80%;
                border: 2px dashed rgba(153, 169, 191, 0.8);
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                top: 10%;
            }
        }
    }
}
</style>