<template>
    <div :class="position">
        <div v-for="(v,i) in arr" :key="i+v.name" class="people" :class="{'active':v.isEdit}">
            <el-popover placement="bottom" trigger="click" v-model="v.isEdit">
                <el-select v-model.trim="v.name" filterable allow-create default-first-option :clearable="true" size="mini" placeholder="请输入" @change="selectChange(v)">
                    <el-option v-for="name in fillArr" :key="name" :label="name" :value="name"></el-option>
                </el-select>
                <div slot="reference">
                    <p class="index">
                        {{v.index}}
                        <i class="icon el-icon-edit"></i>
                    </p>
                    <el-tag :class="{'empty':!v.name}" size="mini" closable :hit="true" @close="close(v)" slot="reference">{{v.name}}</el-tag>
                </div>
            </el-popover>
        </div>
    </div>
</template>
<script>
import { fillArr, fillTest } from "./p.js";

export default {
    name: "",
    data: () => ({
        popoverVisible: false,
        fillArr: fillArr
    }),
    props: {
        arr: {
            type: Array,
            default: () => []
        },
        position: {
            type: String,
            default: "top"
        }
    },
    methods: {
        close(v) {
            v.name = "";
        },
        selectChange(v) {
            v.isEdit = false;
        }
    }
};
</script>
